import { ILivePreview, ICustomLivePreview, IDefaultLivePreview } from '../../../../common/qCloudTemp/livePreview';
import { ICreateLivePreviewRequest, IUpdateLivePreviewRequest } from "../infrastructure/IQCloudApi";

export interface ILivePreviewUseCase {
    createLivePreview(item: ICreateLivePreviewRequest): Promise<ILivePreview>;
    updateLivePreview(ID: string, item: IUpdateLivePreviewRequest): Promise<void>;
    updateSelectedLivePreview(appID: string): PromiseLike<void>;
    getLivePreviewInfo(appID: string, options?: { selected?: boolean; type?: ILivePreview["type"]; }): Promise<ICustomLivePreview | IDefaultLivePreview>;
    listLivePreviewInfo(appID: string): Promise<Array<IDefaultLivePreview | ICustomLivePreview>>;
    getDefaultLivePreviewInfo(appID: string, options?: { selected?: boolean; }): Promise<IDefaultLivePreview>;
    getCustomLivePreviewInfo(appID: string, options?: { selected?: boolean; }): Promise<ICustomLivePreview>;
    startLivePreview(element: HTMLElement, previewId: string): Promise<void>;
}

export const ILivePreviewUseCase = Symbol.for("ILivePreviewUseCase");